<template>
	<div class="">
		<p class="pages_tab">ÂΩìÂâçÈ°µÔºöÁî®Êà∑ÁÆ°ÁêÜ<i class="el-icon-arrow-right"></i>Áà±Ë±ÜÂÆ°Ê†∏</p>
		<!-- Á≠õÈÄâÂå∫Âüü -->
    <el-form label-width="80px" :inline="true">
      <el-form-item label="Ê≠åÂçïÊ†áÁ≠æ">
			  <el-select v-model="Stag" clearable placeholder="Ê≠åÂçïÊ†áÁ≠æÁ≠õÈÄâ">
			    <el-option
			      key="Hot"
			      label="ÁÉ≠Èó®Ê≠åÂçï"
			      value="Hot">
			    </el-option>
			    <el-option
			      key="HotSinger"
			      label="‰∫∫Ê∞îÊ≠åÊâã"
			      value="HotSinger">
			    </el-option>
			  </el-select>
			</el-form-item>
			<el-form-item label="Ê≠åÂçïÂêçÁß∞">
				<el-input placeholder="Ê≠åÂçïÂêçÁß∞" v-model="Stitle"></el-input>
			</el-form-item>
			<el-button type="success" icon="el-icon-search" @click="handleCurrentChange()">ÊêúÁ¥¢</el-button>
			<el-button type="primary" style="float:right" icon="el-icon-edit" @click="editSong()">Ê∑ªÂä†Ê≠åÂçï</el-button>
    </el-form>
		<el-table
			align="center"
			header-align="center"
	    :data="songList"
	    height="calc(100vh - 360px)"
	    :row-class-name="tableRowClassName">
	    <el-table-column
	      prop="id"
	      label="ID">
	    </el-table-column>
	    <el-table-column
	      prop="title"
	      label="Ê≠åÂêç">
	    </el-table-column>
	    <el-table-column
	      prop="description"
	      label="ÊèèËø∞">
	    </el-table-column>
	    <el-table-column
	      prop="TAG"
	      label="Ê†áÁ≠æ">
	      <template slot-scope="scope">
		    		<span v-if="scope.row.TAG == 'Hot'">ÁÉ≠Èó®Ê≠åÂçï</span>
		    		<span v-if="scope.row.TAG == 'HotSinger'">‰∫∫Ê∞îÊ≠åÊâã</span>
		    	</template>
	    </el-table-column>
	    <el-table-column
	      prop="img"
	      label="Â∞ÅÈù¢Âõæ">
	      <template slot-scope="scope">
	    		<img :src="scope.row.img" alt="" @click="showBigImg(scope.row.img)" class="song_img">
	    	</template>
	    </el-table-column>
	    <el-table-column
	      prop="original_id"
	      label="Ê≠åÂçïÈìæÊé•">
	      <template slot-scope="scope">
	    		<a :href="'https://www.youtube.com/playlist?list='+scope.row.original_id" target="_blank">https://www.youtube.com/playlist?list={{scope.row.original_id}}</a>
	    	</template>
	    </el-table-column>
	    <el-table-column
		      fixed="right"
		      label="Â§ÑÁêÜ"
		      width="220">
		      <template slot-scope="scope">
		         <el-button plain type="success" v-if="scope.row.status == 1" @click="editStatus(0,scope.row.id)">Â∑≤‰∏äÁ∫ø</el-button>
		         <el-button plain type="danger" v-if="scope.row.status == 0" @click="editStatus(1,scope.row.id)">Â∑≤‰∏ãÁ∫ø</el-button>
		         <el-button plain type="warning" @click="editSong(scope.row)">‰øÆ Êîπ</el-button>
		      </template>
		    </el-table-column>
	  </el-table>
	  <el-pagination
	  	:current-page="currentPage"
	  	background
	  	@current-change="handleCurrentChange"
	    layout="prev, pager, next"
	    :total="total">
	  </el-pagination>
        <!-- Êü•ÁúãbannerÂ§ßÂõæ -->
	    <el-dialog
          title="Êü•ÁúãÂ§ßÂõæ"
          width="500px"
          :visible.sync="dialogVisible">
          <img :src="dialogImg" style="width: 100%;">
          <span slot="footer" class="dialog-footer">
            <el-button type="danger" @click="dialogVisible = false">ÂÖ≥ Èó≠</el-button>
          </span>
        </el-dialog>
        <!-- ‰∏ãÁ∫øÊ≠åÂçï -->
        <el-dialog
          width="500px"
          :title="title"
          :visible.sync="addVisible">
          <img :src="dialogImg" style="width: 100%;">
          <span slot="footer" class="dialog-footer">
            <el-button type="danger" @click="pushStatus()" :loading="loading2">Á°Æ ÂÆö</el-button>
          </span>
        </el-dialog>
        <!-- ‰øÆÊîπ/Ê∑ªÂä†Ê≠åÂçï -->
        <el-dialog
          :title="'ÁºñËæëÊ≠åÂçï'"
          :visible.sync="visible2">
          <el-form :rules="rules"label-width="80px">
	          <el-form-item label="Ê≠åÂçïID">
					    <el-input :disabled="true" v-model="song.id"></el-input>
					  </el-form-item>
					  <el-form-item label="Ê≠åÂçïÂêçÁß∞" prop="title">
					    <el-input v-model="song.title"></el-input>
					  </el-form-item>
					  <el-form-item label="Ê≠åÂçïÈìæÊé•" prop="link">
					    <el-input v-model="song.link"></el-input>
					  </el-form-item>
					  <el-form-item label="Ê≠åÂçïÊèèËø∞">
					    <el-input v-model="song.description"></el-input>
					  </el-form-item>
					  <el-form-item label="Ê≠åÂçïÊ†áÁ≠æ">
              <el-radio-group v-model="song.TAG">
                <el-radio :label="'HotSinger'">‰∫∫Ê∞îÊ≠åÊâã</el-radio>
                <el-radio :label="'Hot'">ÁÉ≠Èó®Ê≠åÂçï</el-radio>
                <el-radio :label="''">ÂÖ∂‰ªñ</el-radio>
              </el-radio-group>
            </el-form-item>
					  <el-form-item label="Â∞ÅÈù¢Âõæ‰∏ä‰º†" prop="img">
					  	<el-upload class="avatar-uploader" :action="hosts" :show-file-list="false" :on-success="handleImg">
	            <img v-if="song.img" :src="song.img" class="avatar">
	            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
	            </el-upload>
					  </el-form-item>
					  <el-form-item label="‰∏≠ÊñáÊèèËø∞">
					    <el-input v-model="song.original_name"></el-input>
					  </el-form-item>
					  <el-form-item label="ÊòØÂê¶‰∏äÁ∫ø">
              <el-radio-group v-model="song.status">
                <el-radio :label="0">Âê¶</el-radio>
                <el-radio :label="1">ÊòØ</el-radio>
              </el-radio-group>
	           <!--  <el-switch
	              @change="changeStatus"
							  v-model="song.status"
							  active-color="#13ce66"
    						inactive-color="#ff4949"
							  active-value="1"
							  inactive-value="0">
							</el-switch> -->
            </el-form-item>
			  	</el-form>
          <span slot="footer" class="dialog-footer">
            <el-button type="success" @click="visible2 = false" :loading="loading2">Âèñ Ê∂à</el-button>
            <el-button type="danger" @click="pushSong()" :loading="loading2">Á°ÆÂÆö</el-button>
          </span>
        </el-dialog>
	</div>
</template>
<script type="text/javascript">
	import http from '../../utils/http.js';
	require('../../utils/common.js');
	export default {
		data() {
			return {
				songList:[],
				dialogVisible: false,
				dialogImg :'',
				addVisible: false,
				loading2: false,
				visible2: false,
				currentPage: 1,
				id:'',
				total: 0,
				idolname: '',
				Stitle: '',
				title: '',
				Stag: '',
				hosts: 'http://opt.groupy.cn/api/fileupload',
				Status: 1,
				idolType: 'idol',
				song: {
					id: '',
          title: '',
          description: '',
          img: '',
          TAG: '',
          original_name: '',
          link: '',
          status: 1
        },
        rules: {
          title: [
            { required: true, message: 'ËØ∑ËæìÂÖ•Ê≠åÂçïÂêçÁß∞', trigger: 'blur' }
          ],
          link: [
            { required: true, message: 'ËØ∑ËæìÂÖ•Ê≠åÂçïÈìæÊé•', trigger: 'blur' }
          ],
          img: [
            { required: true, message: 'ËØ∑‰∏ä‰º†Â∞ÅÈù¢Âõæ', trigger: 'blur' }
          ]
        },
			}
		},
		methods: {
			handleImg(res, file) {
        this.song.img = res.url;
      },
			tableRowClassName({row, index}) {
		        if(row.status == '0') {
		        	return 'del-row';
		        }
		          return '';
		    },
			showBigImg(val) {
        this.dialogImg = val;
        this.dialogVisible = true;
		    },
      editStatus(val,id) {
        this.addVisible = true;
        this.Status = val;
        this.id = id;
        if(val == 1) {
        	this.title = `‰Ω†Á°ÆÂÆöË¶Å‰∏äÁ∫øID‰∏∫${id}Ëøô‰∏™Ê≠åÂçïÂêóÔºüÔºü`
        }else {
        	this.title = `‰Ω†Á°ÆÂÆöË¶Å‰∏ãÁ∫øID‰∏∫${id}Ëøô‰∏™Ê≠åÂçïÂêóÔºüÔºü`
        }
      },
      pushStatus() {
        var self = this;
        self.loading2 = true;
        http.get('/api/playlist/editStatus',{
        	params: {
        		id: self.id,
        		status: self.Status
        	}
        }).then(function(res){
	      	self.loading2 = false;
        	if(res.success) {
        		self.handleCurrentChange(self.currentPage);
		        self.addVisible = false
		      	if(self.Status == 0) {
		      		self.$message({
			          message: 'üò≠Ôºå‰Ω†ÂÜç‰πü‰∏ç‰ºöÂú®appÈáåÈù¢ÁúãÂà∞Ëøô‰∏™Ê≠åÂçï‰∫ÜÔºÅ',
			          type: 'success'
			        });
		      	}else {
			      	self.$message({
			          message: 'üòÉÔºå‰Ω†ÂæàÂø´Â∞±ÂèØ‰ª•Âú®appÈáåÈù¢ÁúãÂà∞Ëøô‰∏™Ê≠åÂçï‰∫ÜÔºÅ',
			          type: 'success'
			        });
		      	}
        	}else {
        		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
        	}
      	}).catch(err => {
      		self.loading2 = false;
      		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
      	})
      },
      handleCurrentChange(val) {
      	var self = this;
      	const loading = self.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
      	if(!val) {
      		self.currentPage = 1;
      	}else{
      		self.currentPage = val;
      	}
      	console.log(self.Stitle)
      	http.get('/api/playlist/list',{
      		params: {
            pageNum: 10,
      			page: self.currentPage,
      			TAG: self.Stag,
      			title: self.Stitle
      		}
      	}).then(function(res){
      		console.log(res)
        	self.songList = res.rows;
        	self.total = res.count;
	        loading.close();
      	}).catch(err => {
      		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
      	})
      },
      editSong(obj) {
      	this.visible2 = true;
      	if(obj) {
      		this.song = {
      			id: obj.id,
      			title: obj.title,
	          description: obj.description,
	          img: obj.img,
	          TAG: obj.TAG,
	          original_name: obj.original_name,
	          link: 'https://www.youtube.com/playlist?list=' + obj.original_id,
	          status: obj.status
      		}
      	}else {
      		this.song = {
      			id: '',
      			title: '',
	          description: '',
	          img: '',
	          TAG: '',
	          original_name: '',
	          link: '',
	          status: 1
      		}
      	}
      },
      pushSong() {
        var self = this;
        self.loading2 = true;
        if(self.song.title && self.song.link && self.song.img) {
	        let data_ = {
	        	id: self.song.id,
	        	title: self.song.title,
	          description: self.song.description,
	          img: self.song.img,
	          TAG: self.song.TAG,
	          original_name: self.song.original_name,
	          original_id: self.song.link.match(/.*list=(.*)&?.*/)[1],
	          status: self.song.status
	        }
	        http.post('/api/playlist/edit',data_).then(function(res){
		      	self.loading2 = false;
	        	if(res.success) {
	        		self.handleCurrentChange();
			        self.visible2 = false
		      		self.$message({
			          message: 'üòÑ ‰Ω†ÂæàÂø´Â∞±ÂèØ‰ª•ÁúãÂà∞‰Ω†ÁºñËæëÁöÑËøô‰∏™Ê≠åÂçï‰∫ÜÔºÅÔºÅ',
			          type: 'success'
			        });
	        	}else {
	        		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
	        	}
	      	}).catch(err => {
	      		self.loading2 = false;
	      		self.$message.error('ÊúçÂä°Âô®ÊàñËÄÖÁΩëÁªúÈîôËØØ'+ res);
	      	})
        }else {
        	self.$message({
	          message: 'üò° ÈÇ£‰πàÂ§ßÁöÑÁ©∫Áúã‰∏çÂà∞ÂêóÔºüÔºü',
	          type: 'warning'
	        });
        }
      },
		},
		created: function(){
			var self = this;
			self.hosts = `http://${location.hostname}:4000/api/fileupload`;
    	self.handleCurrentChange();
		}
	}
</script>


